<template>
  <div >
    <br>
    <br>
    <div class="weq">
      <h1 >看完以上介紹有興趣</h1>
    </div>

    <div class="weq">
      <div id="h3"><img class="h" src="https://cdn.discordapp.com/icons/480000987306655744/93a289d52cbcb493bb10f937fe490fdf.webp?size=1024"></div>
    </div>


  </div>
</template>

<script>
import anime from "animejs";
export default {
  methods: {
    animatesircles() {
      anime({
            targets: '.weq',
            translateX: 700,
            delay: anime.stagger(300, {
                easing: 'easeOutQuad'
            })
        });
    }
  },
  mounted(){
    this.animatesircles()
  }
}
</script>




<style>
    #h3 {
        position: relative;
        width: 110px;
        height: 110px;
        overflow: hidden;
        border-radius: 50%;
        display: inline-block;
        vertical-align: middle;
    }

    .h2 {
        display: inline-block;
    }
        .h {
        width: 100%;
        height: auto;
        padding-top: -10px;
    }
  .weq{
    text-align: center;
    width: 200px;

  }
</style>



